<template>
  <swiper @change="changeActiveIndex" :current="activeIndex">
    <swiper-item v-for="(item, index) in labelList" :key="index">
      <ListItem :articleList="articleData[index]"></ListItem>
    </swiper-item>
  </swiper>
</template>

<script>
export default {
  name: "ArticleList",
  props: {
    labelList: {
      type: Array,
      default: () => [],
    },
    activeIndex: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      articleData: {},
    };
  },
  watch: {
    labelList(newValue, oldValue) {
      this._getArticleList(this.activeIndex);
    },
  },
  methods: {
    changeActiveIndex(e) {
      this.$emit("changeActiveIndex", e.detail.current);
      this._getArticleList(e.detail.curren);
    },
    // 获取数据
    async _getArticleList(currentIndex) {
      const res = await this.$http.get_article_list({
        classify: this.labelList[currentIndex].name,
      });
      this.articleData[currentIndex] = res;
    },
  },
};
</script>

<style lang="scss" scoped>
.swiper-container {
  height: 100%;
  .swiper-item {
    height: 100%;
    overflow: hidden;
  }
}
</style>
